<template>
  <div>
    <div>
      <el-button type="primary" @click="add">新建</el-button>
      <el-button type="primary" icon="el-icon-delete"></el-button>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData3"
      stripe
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="100">
      </el-table-column>
      <el-table-column
        label="角色名称"
        min-width="120">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </el-table-column>
      <el-table-column
        prop="account"
        label="创建人"
        min-width="120">
      </el-table-column>
      <el-table-column
        prop="date"
        label="创建时间"
        min-width="150">
      </el-table-column>
      <el-table-column
        label="权限设置"
        min-width="150">
        <template slot-scope="scope">
          <el-button plain
            type="primary"
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      layout="total,prev, pager, next"
      :total="1000">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      msg: 'Welcome to login',
      tableData3: [{
        id: '1',
        authority: 'yi',
        name: '超级管理员',
        account: '王小虎',
        date: '2016-05-03',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        id: '1',
        authority: 'yi',
        name: '超级管理员',
        account: '王小虎',
        date: '2016-05-03',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        id: '1',
        authority: 'yi',
        name: '超级管理员',
        account: '王小虎',
        date: '2016-05-03',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        id: '1',
        authority: 'yi',
        name: '超级管理员',
        account: '王小虎',
        date: '2016-05-03',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        id: '1',
        authority: 'yi',
        name: '超级管理员',
        account: '王小虎',
        date: '2016-05-03',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        id: '1',
        authority: 'yi',
        name: '超级管理员',
        account: '王小虎',
        date: '2016-05-03',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        id: '1',
        authority: 'yi',
        name: '超级管理员',
        account: '王小虎',
        date: '2016-05-03',
        address: '上海市普陀区金沙江路 1518 弄'
      }],
      multipleSelection: []
    }
  },
  mounted () {
//    this.tableData3 = [];
  },
  methods: {
    // 在 methods 方法中触发事件
    add () {
      this.$root.Bus.$emit('dialogOpenListener', {id: 0, currentView: 'addEditRole', dialogFormVisible: true});
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(index, row) {
//      console.log(index, row);
      this.$root.Bus.$emit('dialogOpenListener', {id: row.name, currentView: 'addEditRole', dialogFormVisible: true, title: '编辑', data: row});
    },
    handleCurrentChange(val) {
      console.log('当前页发生改变');
    }
  }
}
</script>

