<template>
  <div class="inner-header">
    <span class="header-right">
      <a style="margin-right:20px; ">重设密码</a>
      <router-link to="/">注销</router-link>
      <img src="../assets/admin.png" alt="">
      <span>{{userName}}</span>
    </span>
  </div>
</template>

<script>
export default{
  data () {
    return {
      userName: 'admin'
    }
  }
}
</script>

<style lang="scss">
  $blue-color: #6392fe;
  .el-header {
    padding: 0;
  }
  .inner-header {
    padding: 0 30px;
    color: #ffffff;
    height: 60px;
    line-height: 60px;
    background: $blue-color url('../assets/logo.png') no-repeat 30px;
    overflow: hidden;
    .header-right {
      float: right;
      img {
        display: inline-block;
        vertical-align: middle;
        margin: 0 20px;
      }
    }
  }
</style>



