<template>
  <div>
    <el-form :model="form" :rules="rules" ref="form">
      <el-form-item label="角色名称" :label-width="formLabelWidth" prop="name">
        <el-input v-model="form.name" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="权限设置" prop="authority" :label-width="formLabelWidth">
        <el-select v-model="form.authority" placeholder="请选择">
          <el-option label="权限一" value="yi"></el-option>
          <el-option label="权限二" value="er"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="submit('form')">确 定</el-button>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    id: 0,
    model: {
      type: Object,
      default: function () {
        return { }
      }
    },
  },
  data () {
    return {
      form: {
        id: 0,
        name: '',
        authority: ''
      },
      rules: {
        name: [
          {required: true, message: '请输入角色名称', trigger: 'blur'},
          {min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        authority: [
          { required: true, message: '请选择权限', trigger: 'change' }
        ]
      },
      formLabelWidth: '120px'
    }
  },
  mounted () {
    if (this.id) {
      for (var item in this.form) {
        this.form[item] = this.model[item];
      }
    }
  },
  methods: {
    submit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$root.Bus.$emit('dialogCloseListener')
        }
      });
    },
    cancel () {
      this.$root.Bus.$emit('dialogCloseListener')
    }
  }
}
</script>



